.slick-list {
    margin: 0 -10px; }


.article-title {
    max-width: 200px; }



@mixin text_overflow_white {
    &:after {
        position: absolute;
        content: '';
        width: 80px;
        height: 18px;
        background-image: linear-gradient(to right, #ffffff00, #ffffff 50%, #ffffff);
        right: 0;
        bottom: 0;
        z-index: 99; } }

@mixin text_overflow_blue {
    &:after {
        position: absolute;
        content: '';
        width: 80px;
        height: 18px;
        background-image: linear-gradient(to right, #f2f4ff00, $color-light-blue 50%, $color-light-blue);
        right: 0;
        bottom: 0;
        z-index: 99; } }

@mixin box_shadow($shadow) {
	-webkit-box-shadow: $shadow;
	-moz-box-shadow: $shadow;
	box-shadow: $shadow; }

@mixin scrollbar {
	&::-webkit-scrollbar {
		width: 0.5em; }
	&::-webkit-scrollbar-track {
		@include box_shadow(inset 0 0 6px $color-light-blue); }
	&::-webkit-scrollbar-thumb {
		background-color: $color-light-blue;
		outline: 1px solid $color-light-blue; } }

@mixin hidden_tag {
    visibility: hidden;
    opacity: 0; }

@mixin visible_tag {
    visibility: visible;
    opacity: 1; }

